import { Button, Col, Form, Input, Row } from 'antd';
import moment from 'moment';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/auth/actions';

export default function POSSetupPage() {
	const history = useHistory();
	const dispatch = useDispatch();

	const setup = JSON.parse(localStorage.getItem('setup') || '{}');
	const app = JSON.parse(localStorage.getItem('app') || '{}');

	const emailRef = useRef(null);
	const passwordRef = useRef(null);
	const [form] = Form.useForm();

	const { loading = false, isGrantedAccess = false } = useSelector((state) => state?.auth);
	const { loadingSync = false } = useSelector((state) => state?.sync);
	const { property = {}, isLog = false } = app;

	const handleKeydown = (e) => {
		const isActiveEmail = document.activeElement === (emailRef.current !== null && emailRef.current.input) || false;
		const isActivePassword = document.activeElement === (passwordRef.current !== null && passwordRef.current.input) || false;

		if (e.key === 'Enter' && isActiveEmail) {
			emailRef.current.blur();
			passwordRef.current.focus();
			return;
		}

		if (e.key === 'Enter' && isActivePassword) {
			emailRef.current.blur();
			passwordRef.current.focus();

			return;
		}

		if (e.key === 'Escape') {
			history.push('/pos/auth/default');
			form.resetFields();
		}

		if (e.key === 'ArrowUp') {
			emailRef.current.focus();
		}

		if (e.key === 'ArrowDown') {
			passwordRef.current.focus();

			if (isActivePassword) {
				passwordRef.current.blur();
				emailRef.current.blur();
			}
		}
	};

	const onFinish = (values) => {
		if (!isGrantedAccess) {
			dispatch({
				type: 'auth/USER_ACCESS',
				payload: {
					email: values?.email,
					password: window.btoa(values?.password),
					funcId: '1098'
				},
				history
			});

			return;
		}

		const payload = {
			outletCode: values?.outletCode,
			baseUrl: values.baseUrl,
			header1: values.header1,
			header2: values.header2,
			header3: values.header3,
			header4: values.header4,
			header5: values.header5,
			header6: values.header6,
			footer1: values.footer1,
			footer2: values.footer2,
			footer3: values.footer3,
			footer4: values.footer4,
			footer5: values.footer5,
			footer6: values.footer6,
			refDate: values.refDate
		};

		dispatch({
			type: actions.AUTH_UPDATE_PROPERTY,
			payload
		});
	};

	useEffect(() => {
		// if (!isLog) {
		// 	dispatch({
		// 		type: 'auth/LOGOUT_POS',
		// 		history
		// 	});

		// 	return;
		// }

		const isProperty = Object.keys(property).length !== 0;
		dispatch({
			type: actions.SET_STATE,
			payload: {
				isUpdatedProperty: false,
				isGrantedAccess: false
			}
		});

		if (isProperty) {
			const {
				urlSyncCloud,
				outletCode,
				header1,
				header2,
				header3,
				header4,
				header5,
				header6,
				footer1,
				footer2,
				footer3,
				footer4,
				footer5,
				footer6,
				refDate
			} = property;

			form.setFieldsValue({
				outletCode,
				baseUrl: urlSyncCloud,
				header1,
				header2,
				header3,
				header4,
				header5,
				header6,
				footer1,
				footer2,
				footer3,
				footer4,
				footer5,
				footer6,
				refDate
			});
		}

		document.addEventListener('keydown', handleKeydown);

		return () => {
			document.removeEventListener('keydown', handleKeydown);
		};
	}, []);

	return (
		<div className="w-full h-screen overflow-scroll">
			<div className="w-full h-full flex items-center justify-center">
				<div className={`${isGrantedAccess ? 'w-2/3 h-full' : 'max-w-xl'} py-10`}>
					<div className="max-w-xl">
						<h1 className="text-2xl font-medium">Setting Point of Sales</h1>
						<p className="text-base leading-snug mt-3">
							Mohon untuk melakukan setup awal sebelum login, dan atau ketika pos baru pertama kali di install.
						</p>
					</div>
					{isGrantedAccess ? (
						<div className="w-full mt-7 grid grid-cols-12 gap-5">
							<div className="col-span-8">
								<Form
									form={form}
									name="opening"
									layout="vertical"
									initialValues={{
										remember: true
									}}
									onFinish={onFinish}
									onFinishFailed={console.error}
									autoComplete="off"
									style={{ maxWidth: 'none', width: '100%' }}>
									<div className="pt-3 mb-2 border-t-2 border-gray-300">
										<h1 className="text-lg font-semibold">General Setup</h1>
									</div>
									<Form.Item initialValue={setup?.outletCode} label="Outlet Code" name="outletCode">
										<Input autoFocus placeholder="EL01" className="w-full h-16 rounded-md" />
									</Form.Item>
									<div className="w-full flex items-center gap-3">
										<Form.Item initialValue={setup?.baseUrl} label="Base Url" name="baseUrl" className="w-full">
											<Input placeholder="https://api-backoffice.domain.com" className="w-full h-16 rounded-md" />
										</Form.Item>
									</div>
									<Form.Item initialValue={moment(property?.refDate).format('MM-DD-YYYY')} label="Bussiness Date" name="refDate">
										<Input type="date" placeholder="EL01" className="w-full h-16 rounded-md" />
									</Form.Item>
									<div className="pt-3 mb-2 border-t-2 border-gray-300">
										<h1 className="text-lg font-semibold">Printer Bill</h1>
									</div>
									<div className="w-full grid grid-cols-2 gap-3">
										<Form.Item initialValue={property?.header1 || ''} label="Header 1" name="header1" className="col-span-2">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.header2 || ''} label="Header 2" name="header2">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.header3 || ''} label="Header 3" name="header3">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.header4 || ''} label="Header 4" name="header4">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.header5 || ''} label="Header 5" name="header5">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.header6 || ''} label="Header 6" name="header6">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.footer1 || ''} label="Footer 1" name="footer1" className="col-span-2">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.footer2 || ''} label="Footer 2" name="footer2">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.footer3 || ''} label="Footer 3" name="footer3">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.footer4 || ''} label="Footer 4" name="footer4">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.footer5 || ''} label="Footer 5" name="footer5">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
										<Form.Item initialValue={property?.footer6 || ''} label="Footer 6" name="footer6">
											<Input maxLength={30} placeholder="type here..." className="w-full h-16 rounded-md" />
										</Form.Item>
									</div>
									<div className="w-3/4 flex items-center gap-2">
										<Form.Item className="w-3/4">
											<Button
												disabled={loading || loadingSync}
												loading={loading || loadingSync}
												htmlType="submit"
												className="btn-page">
												Simpan
											</Button>
										</Form.Item>
										<Form.Item>
											<Button className="h-14 px-7 rounded-md uppercase" onClick={() => history.push('/pos/auth/default')}>
												Kembali
											</Button>
										</Form.Item>
									</div>
								</Form>
							</div>
							<div className="col-span-4">
								<div className="w-full h-auto rounded-md bg-white px-3 py-5">
									<div>
										<p className="text-center text-print-medium font-bold">
											{form.getFieldValue('header1') || property?.header1 || 'Header1'}
										</p>
										<p className="text-center text-print-small font-semibold">
											{form.getFieldValue('header2') || property?.header2 || 'Header2'}
										</p>
										<p className="text-center text-print-xsmall mt-2">
											{form.getFieldValue('header3') || property?.header3 || 'Header3'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('header4') || property?.header4 || 'Header4'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('header5') || property?.header5 || 'Header5'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('header6') || property?.header6 || 'Header6'}
										</p>
									</div>

									<div className="my-3 w-full border border-separate border-gray-400" />

									<Row>
										<Col span={7}>
											<p className="text-left text-print-xsmall">Tanggal/Waktu</p>
										</Col>
										<Col span={2}>
											<p className="text-center text-print-xsmall">:</p>
										</Col>
										<Col span={15}>
											<p className="text-left text-print-xsmall">{moment(new Date()).format('ddd, DD MMMM YYYY LTS')}</p>
										</Col>
									</Row>
									<Row className="my-1">
										<Col span={7}>
											<p className="text-left text-print-xsmall">No. Transaksi</p>
										</Col>
										<Col span={2}>
											<p className="text-center text-print-xsmall">:</p>
										</Col>
										<Col span={15}>
											<p className="text-left text-print-xsmall">{`${form.getFieldValue('outletCode') || 'OC'}00000001`}</p>
										</Col>
									</Row>
									<Row>
										<Col span={7}>
											<p className="text-left text-print-xsmall">Kasir</p>
										</Col>
										<Col span={2}>
											<p className="text-center text-print-xsmall">:</p>
										</Col>
										<Col span={15}>
											<p className="text-left text-print-xsmall">Guest</p>
										</Col>
									</Row>

									<div className="my-3 w-full border border-separate border-gray-400" />

									<Row wrap gutter={[2, 2]} className="mb-2">
										<Col span={24}>
											<p className="text-left text-print-xsmall">Beras Rojolele 25 Kg</p>
										</Col>
										<Col span={8}>
											<p className="text-left text-print-xsmall">Rp 275.000</p>
										</Col>
										<Col span={8}>
											<p className="text-center text-print-xsmall">1x</p>
										</Col>
										<Col span={8}>
											<p className="text-right text-print-xsmall">Rp 275.000</p>
										</Col>
									</Row>
									<Row wrap gutter={[2, 2]} className="mb-2">
										<Col span={24}>
											<p className="text-left text-print-xsmall">Minyak goreng sanco 2L</p>
										</Col>
										<Col span={8}>
											<p className="text-left text-print-xsmall">Rp 35.000</p>
										</Col>
										<Col span={8}>
											<p className="text-center text-print-xsmall">4x</p>
										</Col>
										<Col span={8}>
											<p className="text-right text-print-xsmall">Rp 140.000</p>
										</Col>
									</Row>

									<div className="my-3 w-full border border-separate border-gray-400" />

									<Row className="my-1">
										<Col span={12}>
											<p className="text-right text-print-xsmall capitalize">CASH</p>
										</Col>
										<Col span={12}>
											<p className="text-right text-print-xsmall uppercase">Rp 415.000</p>
										</Col>
									</Row>
									<Row className="my-1">
										<Col span={12}>
											<p className="text-right text-print-xsmall">Subtotal</p>
										</Col>
										<Col span={12}>
											<p className="text-right text-print-xsmall">Rp 145.000</p>
										</Col>
									</Row>
									<Row className="my-1">
										<Col span={12}>
											<p className="text-right text-print-xsmall">Diskon</p>
										</Col>
										<Col span={12}>
											<p className="text-right text-print-xsmall">Rp 0</p>
										</Col>
									</Row>
									<Row className="my-1">
										<Col span={12}>
											<p className="text-right text-print-xsmall">Total</p>
										</Col>
										<Col span={12}>
											<p className="text-right text-print-xsmall">Rp 145.000</p>
										</Col>
									</Row>
									<Row className="my-1">
										<Col span={12}>
											<p className="text-right text-print-xsmall">Bayar</p>
										</Col>
										<Col span={12}>
											<p className="text-right text-print-xsmall">Rp 150.000</p>
										</Col>
									</Row>
									<Row className="my-1">
										<Col span={12}>
											<p className="text-right text-print-xsmall">Kembalian</p>
										</Col>
										<Col span={12}>
											<p className="text-right text-print-xsmall">Rp 5.000</p>
										</Col>
									</Row>

									<div className="mt-7">
										<p className="text-center text-print-xsmall font-semibold">
											{form.getFieldValue('footer1') || property?.footer1 || 'Footer 1'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('footer2') || property?.footer2 || 'Footer 2'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('footer3') || property?.footer3 || 'Footer 3'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('footer4') || property?.footer4 || 'Footer 4'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('footer5') || property?.footer5 || 'Footer 5'}
										</p>
										<p className="text-center text-print-xsmall">
											{form.getFieldValue('footer6') || property?.footer6 || 'Footer 6'}
										</p>
									</div>
								</div>
							</div>
						</div>
					) : (
						<div className="w-3/4 my-5">
							<Form
								form={form}
								name="login"
								layout="vertical"
								initialValues={{
									remember: true
								}}
								onFinish={onFinish}
								onFinishFailed={console.log}
								autoComplete="off"
								style={{ maxWidth: 'none', width: '100%' }}>
								<Row wrap gutter={[8, 0]}>
									<Col span={24}>
										<Form.Item
											label="Email"
											name="email"
											rules={[
												{
													required: true,
													message: 'Wajib Diisi!'
												}
											]}>
											<Input
												ref={emailRef}
												type="email"
												autoFocus
												autoComplete="false"
												placeholder="usertest@domain.com"
												className="w-full h-12 rounded-md"
											/>
										</Form.Item>
									</Col>
									<Col span={24}>
										<Form.Item
											label="Password"
											name="password"
											rules={[
												{
													required: true,
													message: 'Wajib Diisi!'
												}
											]}>
											<Input.Password
												ref={passwordRef}
												type="password"
												autoComplete="false"
												placeholder="********"
												className="w-full h-12 rounded-md"
											/>
										</Form.Item>
									</Col>
									<Col span={18}>
										<div className="w-full flex items-center justify-start gap-2 mt-3">
											<Form.Item>
												<Button
													disabled={loading}
													loading={loading}
													type="primary"
													htmlType="submit"
													className="w-full bg-primary h-12 rounded-md hover:bg-blue-900">
													Validasi Kasir
												</Button>
											</Form.Item>
											<Form.Item>
												<Button
													disabled={loading}
													type="default"
													htmlType="button"
													className="w-full h-12 flex items-center justify-center gap-2 rounded-md"
													onClick={() => history.push('/pos/auth/default')}>
													Kembali <kbd className="kbd">esc</kbd>
												</Button>
											</Form.Item>
										</div>
									</Col>
								</Row>
							</Form>
						</div>
					)}
				</div>
			</div>
		</div>
	);
}
